.promo-popup-btn-wrapper,
.nav-container {
  display: none; }

.admin-bar .nav-primary {
  top: 32px; }

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1600px;
  height: 100%;
  margin: 0 auto; }

.nav {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  height: 110px;
  margin-bottom: 2rem;
  font-family: Arial, Helvetica, sans-serif;
  z-index: 10;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5); }
  .nav * {
    transition: all ease-in-out 300ms; }
  .nav a,
  .nav span {
    color: inherit;
    padding: 2rem 1rem;
    text-transform: uppercase;
    display: inline-block;
    font-weight: bold;
    position: relative;
    font-size: 1.3rem;
    cursor: pointer; }
    .nav a:hover,
    .nav span:hover {
      text-decoration: none;
      color: #f00; }
  .nav-primary {
    height: 66px;
    position: sticky;
    top: 0;
    background-color: #f9f9f9;
    color: #222;
    z-index: 2; }
    .nav-primary a,
    .nav-primary span {
      font-size: 1.6rem;
      padding: 2.5rem 1rem; }
    .nav-primary .nav-regions {
      margin-left: 50px; }
    .nav-primary .nav-right {
      display: flex;
      flex-direction: row;
      justify-content: flex-end; }
  .nav-secondry {
    height: 55px;
    background-color: #111;
    color: #fff;
    z-index: 1; }
    .nav-secondry .nav-left a:last-child,
    .nav-secondry .nav-left span:last-child {
      position: relative; }
    .nav-secondry .nav-sections {
      margin-left: 235px; }
  .nav-logo {
    padding: 10px 0 !important; }
    .nav-logo img {
      height: 40px; }
  .nav-left, .nav-right {
    position: relative;
    display: flex; }
    .nav-left nav, .nav-right nav {
      height: max-content;
      display: flex;
      flex-direction: row;
      align-items: center; }
  .nav-sub {
    display: none !important;
    position: absolute;
    top: 55px;
    background-color: #222;
    color: #fff;
    width: max-content;
    min-width: 150px;
    padding-top: 1rem;
    transition: all ease-in-out 300ms; }
    .nav-sub a {
      display: block;
      padding: 1.5rem 1rem; }
    .nav-sub .sub:hover > a,
    .nav-sub .sub:hover > span {
      background-color: #333 !important;
      color: #f00; }
    .nav-sub .sub > a::after,
    .nav-sub .sub > span::after {
      content: "\f054" !important; }
    .nav-sub .sub .nav-sub {
      background-color: #333 !important;
      position: absolute;
      left: 100%;
      top: 0; }
  .nav .nav-account a, .nav .nav-account span {
    color: #222 !important;
    font-size: 1.3rem; }
    .nav .nav-account a:hover, .nav .nav-account span:hover {
      color: #f00 !important; }
  .nav .current {
    color: #f00; }
  .nav .nav-social a {
    position: relative;
    padding: 1.3rem 1.5rem;
    color: #eee;
    font-weight: normal;
    font-size: 0; }
    .nav .nav-social a::before {
      display: inline-block;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      font-family: "Font Awesome 5 Brands", "Font Awesome 5 Free";
      font-size: 3rem; }
    .nav .nav-social a[href*="facebook.com"]::before {
      content: '\f09a'; }
    .nav .nav-social a[href*="twitter.com"]::before {
      content: '\f099'; }
    .nav .nav-social a[href*="instagram.com"]::before {
      content: '\f16d'; }
    .nav .nav-social a[href*="youtube.com"]::before {
      content: '\f167'; }
    .nav .nav-social a[href*="linkedin.com"]::before {
      content: '\f08c'; }
    .nav .nav-social a[href*="facebook.com"]:hover::before {
      color: #3b5998; }
    .nav .nav-social a[href*="twitter.com"]:hover::before {
      color: #00aced; }
    .nav .nav-social a[href*="youtube.com"]:hover::before {
      color: #bb0000; }
    .nav .nav-social a[href*="instagram.com"]:hover::before {
      color: #517fa4; }
    .nav .nav-social a[href*="linkedin.com"]:hover::before {
      color: #007bb6; }
  .nav .nav-item {
    position: relative; }
    .nav .nav-item.sub > a,
    .nav .nav-item.sub > span {
      position: relative;
      padding-right: 2rem; }
      .nav .nav-item.sub > a::after,
      .nav .nav-item.sub > span::after {
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f078";
        position: absolute;
        top: 40%;
        right: 5px; }
    .nav .nav-item:hover.sub > a,
    .nav .nav-item:hover.sub > span {
      background-color: #222; }
    .nav .nav-item:hover > .nav-sub {
      display: block !important; }

@media (max-width: 1268px) {
  .nav-primary .nav-regions {
    margin-left: 10px; }
  .nav-secondry .nav-sections {
    margin-left: 195px; } }

@media (max-width: 1225px) {
  .nav-primary a, .nav-primary span {
    font-size: 1.4rem; } }

.nav-search-form {
  position: relative; }

.nav-search-form button {
  display: none;
  position: absolute;
  top: 28%;
  right: 10px;
  background-color: #f1f1f1;
  border: none; }

.nav-search-form .fa {
  color: #000;
  font-size: 2rem; }

.nav-search-label {
  display: block;
  position: absolute;
  top: 30%;
  right: 17px; }

.nav-search:focus ~ .nav-search-label,
.nav-search:active ~ .nav-search-label {
  display: none; }

.nav-search:focus ~ button,
.nav-search:active ~ button {
  display: block; }

.nav-search {
  background-color: transparent;
  border-radius: 0;
  border: none;
  width: 125px;
  max-width: 400px;
  margin-right: 1rem;
  padding: 1.5rem 40px 1.5rem 1rem;
  transition: all ease-in-out 500ms;
  text-transform: uppercase;
  color: #000; }

.nav-search:focus {
  color: #000;
  outline: none;
  width: 350px;
  text-align: left;
  border-bottom: 1px solid #eee; }

.nav-search:focus::placeholder {
  color: #888;
  text-align: left; }

.nav-search::placeholder {
  color: #eee;
  text-transform: uppercase;
  text-align: left; }

nav.nav-account a {
  font-weight: normal;
  font-size: 1.3rem; }

.nav-account a::before {
  content: '';
  position: absolute;
  left: 0;
  height: 30%;
  top: 35%;
  background-color: #999;
  width: 1px; }
